import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { CartService } from 'src/app/service/cart.service';

declare var swal: any;

@Component({
  selector: 'app-cart-add',
  templateUrl: './cart-add.component.html'
})
export class CartAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  productList: any = [];
  offerList: any = [];
  showLoader = false;
  formErrors = {
    product_id: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private cartservice: CartService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      product_id: [1],
      quantity: [null, Validators.compose([Validators.required])],
      total_ammount: [0, Validators.compose([Validators.required])],
      offer_id: [1],    
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.cartservice.getCartById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            product_id: response.data.product_id,
            total_amount: response.data.total_amount,
            quantity: response.data.quantity,
            offer_id: response.data.offer_id,
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/cart');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?).(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].product_id.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.cartservice.editCart(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/cart');
          } else {
            this.showLoader = false;
            response.error.map(obj => {
              if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            });
          }
        });
      } else {
        this.cartservice.addCart(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/cart');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

            if (obj.hasOwnProperty('product_id')) {
                this.formErrors.product_id = obj.product_id;
              }
            })
          }
        });
      }
    }
  }
}
